@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap');
@import url('https://fonts.googleapis.com/css?family=Righteous');

.home-name {
    display: block;
    font-size: 50px;
    margin-top: 20px;
}

.home {
    display: flex;
    justify-content: center;
    text-align: center;
    margin-top: 30px;
    flex-direction: column;
}

.home-title {
    font-size: 30px;
    background-image: linear-gradient(to right top, #ffff34, #ffc812, #ff8f2f, #ff554d, #eb1267);
    background-size: 50% auto;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-family: 'Righteous', serif;
    animation: gradientAnim 2s ease  alternate infinite;
}

@keyframes gradientAnim {
    to {
        background-position: 100% top;
    }
}

.nav-links {
    margin-left: 25px;
    text-decoration: none;
    font-size: 20px;
    color: black;
    font-family: 'Righteous', serif;
}

.nav-links:hover {
    color: white;
    background-color: #3bdee5;
    border-radius: 15%;
}

.header {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    background-color: #81e1d0;
    height: 90px;
}

.my-photo {
    width: 250px;
    height: 250px;
    border-radius: 30px;
}

.main-box {
    text-align: center;
    margin-top: 20px;
}

.about {
    text-align: center;
    margin-top: 50px;
}

.about-box {
    display: flex;
    justify-content: space-between;
    background-color: #daf0f1;
    width: 620px;
    height:280px;
    margin: 0 auto;
    padding: 20px;
    border-radius: 20px;
}

.about-title {
    font-size: 40px;
    font-family: 'Righteous', serif;
    text-shadow: .05em .05em 0 hsl(183, 75%, 52%);
}

.about-my-photo {
    width: 250px;
    height: 250px;
    border-radius: 30px;
}

.about-text {
    width: 340px;
    font-size: 20px;
    line-height: 30px;
    text-align: left;
    font-family: 'Righteous', serif;
}

.experience {
    text-align: center;
    margin-top: 50px;
}

.ex-title {
    font-size: 40px;
    font-family: 'Righteous', serif;
    text-shadow: .05em .05em 0 hsl(183, 75%, 52%);
}

.ex-box {
    width: 500px;
    height: 400px;
    margin: 0 auto;
}

.ex-min-box {
    display: flex;
    justify-content: space-around;
}

.ex-text {
    width: 50%;
    font-size: 30px;
    padding: 20px;
    background-color: #daf0f1;
    margin-top: 15px;
    border-radius: 10px;
    font-family: 'Righteous', serif;
}

.ex-text:first-child{
    margin-right: 15px;
}

.ex-min-text {
    font-size: 15px;
    display: block;
}

.hobby {
    text-align: center;
    margin-top: 70px;
    --space: 2rem;
}

.hobby-title {
    font-size: 40px;
    font-family: 'Righteous', serif;
    text-shadow: .05em .05em 0 hsl(183, 75%, 52%);
}
.hobby-img {
    width: 200px;
    height: 200px;
    border-radius: 50%;
    object-fit: cover;
    aspect-ratio: 1;
}

.hobby-box {
    display: flex;
    justify-content: space-around;
    --duration: 10s;
    --gap: var(--space);
    overflow: hidden;
    user-select: none;
    flex-shrink: 0;
    align-items: center;
    gap: var(--gap);
    min-width: 100%;
    animation: scroll var(--duration) linear infinite;
    animation-direction: reverse;
    animation-delay: calc(var(--duration) / -2);
}

@keyframes scroll {
    0% {
        transform: translateX(0);
    }

    100% {
        transform: translateX(calc(-100% - var(--gap)));
    }
}

.contact {
    text-align: center;
    margin-top: 70px;
}

.contact-title {
    font-size: 40px;
    font-family: 'Righteous', serif;
    text-shadow: .05em .05em 0 hsl(183, 75%, 52%);
}

.contact-logo {
    width: 70px;
    height: 70px;
}

.contact-logo-gmail{
    width: 80px;
    height: 60px;
}

.contact-block {
    display: flex;
    justify-content: space-around;
}

.contact-box {
    width: 500px;
    height: 400px;
    margin: 0 auto;
}

.contact-min-box {
    margin-top: 30px;
    background-color: #daf0f1;
    width: 220px;
    height: 120px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 15px;
}

.footer {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    background-color: #81e1d0;
    height: 90px;
}
